.card {
    max-width: 800px;
    font-size: 14px;;
    .cardContent {
        & > *:not(:last-child) {
            margin-bottom: 18px;
        }
        .title {
            color: #120639;
            font-size: 24px;;
            font-style: italic;
        }
        .fieldRow {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 12px;
            & > span:first-child {
                color: #120639;
                font-weight: 700;
                flex: 0 0 180px;
            }
            & > span:last-child {
                flex: 1;
            }
        }
        .personalDetails {
            width: 500px;;
                & + .boxImage {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    & > img {
                        width: 160px;;
                        height: 160px;
                        border-radius: 5px;
                    }
                    button {
                        border: 0;
                        outline: none;
                        background: inherit;
                        font-size: 12px;
                        cursor: pointer;
                        text-decoration: underline;
                        opacity: .54;
                        transition: .2s;
                        &:hover {
                                opacity: 1;
                        }
                        &:active {
                            transform: scale(.9);
                        }
                    }
                }
        }
        .descriptionExperienceBox {
            width: 60%;
        }
    }
}
